'use strict';

import mongoose from 'mongoose';
import {Schema} from 'mongoose';

var MenuItemSchema = new mongoose.Schema({
  title: {
    type: String
  },
  description: {
    type: String
  },
  category: {
    type: Schema.ObjectId,
    ref: 'Category'
  },
  //This field should have same data as the title field of category
  categoryTitle: {
    type: String
  },
  thumb: {
    type: String
  },
  additionalInfo: {
    type: String
  },
  extraIngredients: {
    type: Array
  },

  //DEVELOPPING
  discount: {
    type: Number
  },
  totalRating: {
    type: Number,
    default: 0
  },
  rating: {
    type: Number,
    default: 0
  },
  noOfRating: {
    type: Number,
    default: 0
  },
  price: {
    type: Array
  },
  //to do sorting
  sortPrice: {
    type: Number
  },
  available: {
    type: Boolean
  },
  createdAt: {
    type: Date,
    default: Date.now
  },
  updatedAt: {
    type: Date
  }
});

export default mongoose.model('MenuItem', MenuItemSchema);
